@import "./src/assets/sass/prepends.scss";

.card {
  margin-bottom: 0.5rem;
}
.pure-menu-heading {
  border-color: $bg-light;
}
h4 time {
  font-size: 0.75rem;
}

.youtube{
  position: relative;
  display: block;
  width: 100%;
  padding: 56.25% 0 0;
  margin-bottom: 1rem;

  iframe{
    position: absolute;
    top: 0;
    left: 0;
    width: 100% !important;
    height: 100% !important;
  }
}
